@font-face {
  font-family: "Hagin Caps Medium";
  font-style: normal;
  font-display: swap;
  font-weight: 500;
  src: url("../fonts/hagincapsmedium.woff2") format("woff2"), url("../fonts/hagincapsmedium.woff") format("woff"); }

@font-face {
  font-family: "Roboto Regular";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url("../fonts/robotoregular.woff2") format("woff2"), url("../fonts/robotoregular.woff") format("woff"); }

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 22px;
  font-weight: 400;
  line-height: 30px;
  color: #817155; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: none;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  max-width: 1366px;
  margin: 0 auto; }
  .page-header--main {
    min-height: 768px;
    background-image: url("../img/background-promo.jpg");
    background-color: #2c2c27;
    background-repeat: no-repeat;
    background-position: 0 1px;
    background-size: cover; }

.page-header__top {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 35px 34px 0 53px; }

.page-header__logo-wrapper {
  width: 184px; }

.page-header__logo {
  display: block;
  width: 184px;
  height: 30px; }

.page-header__top-right {
  display: flex;
  flex-wrap: wrap;
  width: 976px; }

.page-header__main-nav {
  margin-right: auto; }

@media (max-width: 768px) {
  .page-header--main {
    min-height: 568px; } }

.main-nav {
  width: 767px; }

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none; }

.main-nav__item {
  padding-right: 24px; }
  .main-nav__item--accessories {
    padding-right: 26px; }
  .main-nav__item--spares {
    padding-right: 26px; }
  .main-nav__item--workshop {
    padding-right: 25px; }
  .main-nav__item--delivery {
    padding-right: 22px; }

.main-nav__link {
  font-family: "Hagin Caps Medium", "Times New Roman", serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0.04em;
  color: #9b8869;
  text-decoration: none; }
  .main-nav__link--page-main {
    text-transform: uppercase;
    color: #817155; }

.user-item {
  position: relative;
  min-width: 36px;
  height: 36px;
  padding: 0;
  list-style: none; }
  .user-item--profile {
    margin-right: 10px; }
  .user-item--search {
    margin-right: 8px; }
  .user-item--logout {
    display: none; }

.user-item__link {
  display: block;
  min-width: 36px;
  height: 36px;
  color: #9b8869;
  text-decoration: none; }

.user-item__icon {
  position: absolute; }
  .user-item__icon--profile {
    top: 7px;
    left: 9px; }
  .user-item__icon--search {
    top: 8px;
    left: 9px; }
  .user-item__icon--basket {
    top: 7px;
    left: 7px; }

.button {
  display: block;
  padding: 21px 20px 16px;
  font-family: "Hagin Caps Medium", "Times New Roman", serif;
  font-size: 21px;
  font-weight: 500;
  font-style: normal;
  line-height: 21px;
  letter-spacing: 0.08em;
  color: #baa480;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  border: 1px solid #baa480; }

.promo__slogan {
  width: 508px;
  height: 146px; }
